.nav-buttons-list {
  width: 310px;
  gap: 2px;
  margin: 0 auto;
  display: flex;
}

.hide {
  display: none;
}

.show {
  display: block;
}

.nav-buttons {
  letter-spacing: .04em;
  color: var(--hover-btnText);
  background: var(--brand-color);
  border: 1px solid var(--brand-color);
  border-radius: 4px;
  padding: 4px 8px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.nav-buttons:hover, .nav-buttons:focus {
  color: var(--hover-btnText);
  background-color: var(--hover-color);
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

@media screen and (min-width: 767px) {
  .nav-buttons-list {
    justify-content: center;
  }

  .nav-buttons {
    padding: 8px 24px;
  }
}

@media screen and (min-width: 1439px) {
  .nav-buttons-list {
    gap: 24px;
    position: absolute;
    top: -10%;
    left: 50%;
    transform: translate(-50%, 50%);
  }

  .nav-buttons {
    font-size: 18px;
  }
}

