.btn-up {
  width: 40px;
  height: 40px;
  z-index: 9999;
  cursor: pointer;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  opacity: .9;
  box-shadow: 1px 1px 4px var(--brand-color);
  background-color: rgba(255, 255, 255, .5);
  background-size: cover;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 35px;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 25px;
  right: 35px;
}

.btn-up:hover {
  opacity: 1;
  background-color: var(--hover-color);
  -webkit-backdrop-filter: blur(1px);
  backdrop-filter: blur(1px);
  box-shadow: none;
  transform: scale(1.1);
}

.scroll-up-svg {
  stroke: var(--brand-color);
  fill: rgba(0, 0, 0, 0);
  transition: all .3s ease-in-out;
  transform: rotate(90deg);
}

.btn-up:hover .scroll-up-svg {
  stroke: var(--hover-btnText);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader-container {
  width: 100%;
  height: 100%;
  background: var(--backdrop-color-loader, rgba(236, 235, 235, .1));
  z-index: 999;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.flex {
  gap: 20px;
  display: flex;
}

.current {
  color: var(--brand-color);
}

